/**
 *    ( latex).
 *
 *     .
 *   :         
 * @param text  
 * @param newLine   
 * @returns     
 */
export function parseNewLines(newLine) {
    return (text) => {
        const result = [];
        const brs = text.match(/\n\n+/g);
        if (!brs) {
            return [text];
        }
        let tail = text;
        let i;
        for (const br of brs) {
            i = tail.indexOf(br);
            const subbrs = [];
            if (br.length === 2) {
                subbrs.push(newLine);
            }
            else {
                for (let j = 0; j < 2 * br.length - 4; j++) {
                    subbrs.push(newLine);
                }
            }
            result.push(tail.slice(0, i), ...subbrs);
            tail = tail.slice(i + br.length);
        }
        if (tail) {
            result.push(tail);
        }
        return result;
    };
}
